import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			popupStatus: false,
			calendarStatus: false,
			status: true,
			statuss: false,
			name: '',
			phone: '',
			sex: '男',
			birthday: '',
			stature: '',
			weight: '',
			remark: '',
			dataTime: ''
		};
	},
	mounted() {
		this.dataTime = this.getNowFormatDate()
	},
	methods:{
		// 获取当前时间
		getNowFormatDate() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var strDate = date.getDate();
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = "0" + strDate;
			}
			var currentdate = year + seperator1 + month + seperator1 + strDate;
			return currentdate;
		},
		toSkip() {
			this.popupStatus = false
			uni.redirectTo({
				url: '../MemberDeepButler/MemberDeepButler'
			})
		},
		statusClick(value) {
			if (value == 1) {
				this.status = true
				this.statuss = false
				this.sex = '男'
			} else {
				this.status = false
				this.statuss = true
				this.sex = '女'
			}
		},
		openCalendar () {
			this.$refs.calendar.open();
			this.calendarStatus = true;
		},
		close() {
			this.calendarStatus = false
		},
		confirm(e) {
			this.birthday = e.fulldate
		},
		submit() {
			if (this.name == '') {
				uni.showToast({
					icon: 'none',
					title: '姓名不能为空',
					duration: 1000
				})
				return false
			}
			if (this.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '电话不能为空',
					duration: 1000
				})
				return false
			}
			if (this.birthday == '') {
				uni.showToast({
					icon: 'none',
					title: '出生日不能为空',
					duration: 1000
				})
				return false
			}
			if (this.stature == '') {
				uni.showToast({
					icon: 'none',
					title: '身高不能为空',
					duration: 1000
				})
				return false
			}
			if (this.weight == '') {
				uni.showToast({
					icon: 'none',
					title: '体重不能为空',
					duration: 1000
				})
				return false
			}
				 // 加载动画
				uni.showLoading({
					title: '提交中'
				});
				this.$member.post(
					'Store/insert_steward_material',
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="name"' +
					'\r\n' +
					'\r\n' + this.name +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="phone"' +
					'\r\n' +
					'\r\n' + this.phone +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="sex"' +
					'\r\n' +
					'\r\n' + this.sex +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="birthday"' +
					'\r\n' +
					'\r\n' + this.birthday +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="stature"' +
					'\r\n' +
					'\r\n' + this.stature +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="weight"' +
					'\r\n' +
					'\r\n' + this.weight +
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="remark"' +
					'\r\n' +
					'\r\n' + this.remark +
					'\r\n--XXX--').then(res => {
					// 关闭加载动画
					uni.hideLoading();
					if (res.data.code == 200) {
						this.popupStatus = true
					} else {
						 uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
						 })
					}
				})
		}
	}
}
